<template>
    <div class="login-container">
        <h1>통합 로그인</h1>

        <!-- 구글 로그인 -->
        <div class="login-section">
            <h2>구글 로그인</h2>
            <button v-if="!googleLoggedIn" @click="googleLogin" class="google-button">
                <img src="@/assets/google-login.png" alt="Login with Google" class="google-button-img" />
            </button>
            <button v-if="googleLoggedIn" @click="googleLogout" class="logout-button google-logout">
                구글 로그아웃
            </button>
        </div>

        <!-- 카카오 로그인 -->
        <div class="login-section">
            <h2>카카오 로그인</h2>
            <a v-if="!kakaoLoggedIn" @click="kakaoLogin" class="kakao-button">
                <img
                    src="https://k.kakaocdn.net/14/dn/btroDszwNrM/I6efHub1SN5KCJqLm1Ovx1/o.jpg"
                    width="222"
                    alt="카카오 로그인 버튼"
                />
            </a>
            <button v-if="kakaoLoggedIn" @click="kakaoLogout" class="logout-button kakao-logout">
                카카오 로그아웃
            </button>
        </div>

        <!-- 네이버 로그인 -->
        <div class="login-section">
            <h2>네이버 로그인</h2>
            <div id="naverIdLogin" v-if="!naverLoggedIn"></div>
            <button v-if="naverLoggedIn" @click="naverLogout" class="logout-button naver-logout">
                네이버 로그아웃
            </button>
        </div>

        <!-- 로그인 상태 표시 -->
        <div class="status-section">
            <h3>로그인 상태</h3>
            <p>구글: {{ googleLoggedIn ? '로그인됨' : '로그아웃됨' }}</p>
            <p>카카오: {{ kakaoLoggedIn ? '로그인됨' : '로그아웃됨' }}</p>
            <p>네이버: {{ naverLoggedIn ? '로그인됨' : '로그아웃됨' }}</p>
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted, nextTick } from 'vue';

// 환경 변수
const googleClientId = process.env.VUE_APP_OAUTH_CLIENT;
const kakaoKey = process.env.VUE_APP_KAKAO_KEY || '33d8c15972efdd0260e97b12ff581cd9';
const naverClientId = process.env.VUE_APP_NAVER_CLIENT_ID || 'auC8kDcoRx6AZ7q_uI6Z';

// 로그인 상태
const googleLoggedIn = ref(false);
const kakaoLoggedIn = ref(false);
const naverLoggedIn = ref(false);

// 네이버 로그인 객체
let naverLogin = null;
let naverAccessToken = null;

onMounted(() => {
    initializeAll();
});

const initializeAll = async () => {
    await initializeKakao();
    await initializeNaver();
    console.log('모든 로그인 서비스 초기화 완료');
};

// === 구글 로그인 ===
const googleLogin = () => {
    console.log('=== 구글 로그인 시도 ===');

    if (!googleClientId) {
        alert('구글 OAuth 클라이언트 ID가 설정되지 않았습니다.');
        return;
    }

    const checkGoogleLoaded = () => {
        if (window.google && window.google.accounts && window.google.accounts.oauth2) {
            console.log('✅ Google Identity Services 로드 완료');

            try {
                const client = window.google.accounts.oauth2.initCodeClient({
                    client_id: googleClientId,
                    scope: 'email profile openid',
                    ux_mode: 'popup',
                    callback: handleGoogleAuthResponse
                });

                console.log('✅ 구글 OAuth 클라이언트 초기화 완료');
                client.requestCode();
            } catch (error) {
                console.error('❌ 구글 OAuth 클라이언트 초기화 오류:', error);
                alert('구글 OAuth 클라이언트 초기화에 실패했습니다: ' + error.message);
            }
        } else {
            console.log('⏳ Google Identity Services 로드 대기 중...');
            setTimeout(checkGoogleLoaded, 100);
        }
    };

    checkGoogleLoaded();
};

const handleGoogleAuthResponse = (response) => {
    console.log('=== 구글 로그인 응답 ===');
    console.log('전체 응답:', response);

    if (response.code) {
        console.log('✅ 구글 로그인 성공! 코드:', response.code);
        googleLoggedIn.value = true;
        alert('구글 로그인 성공!');
    } else if (response.error) {
        console.error('❌ 구글 로그인 오류:', response.error);
        alert('구글 로그인에 실패했습니다: ' + response.error);
    } else {
        console.error('❌ 알 수 없는 응답:', response);
        alert('알 수 없는 응답을 받았습니다.');
    }
};

const googleLogout = () => {
    console.log('=== 구글 로그아웃 ===');
    googleLoggedIn.value = false;
    alert('구글 로그아웃되었습니다.');
};

// === 카카오 로그인 ===
const initializeKakao = async () => {
    if (window.Kakao && !window.Kakao.isInitialized()) {
        window.Kakao.init(kakaoKey);
        console.log('✅ 카카오 초기화 완료');
    }
};

const kakaoLogin = () => {
    console.log('=== 카카오 로그인 시도 ===');

    try {
        window.Kakao.Auth.login({
            scope: 'profile_image, profile_nickname',
            success: getKakaoAccount,
            fail: (error) => {
                console.error('카카오 로그인 실패:', error);
                alert('카카오 로그인에 실패했습니다. 다시 시도해주세요.');
            }
        });
    } catch (error) {
        console.error('카카오 로그인 초기화 오류:', error);
        alert('카카오 로그인을 초기화하는 중 오류가 발생했습니다.');
    }
};

const getKakaoAccount = () => {
    window.Kakao.API.request({
        url: '/v2/user/me',
        success: (res) => {
            const kakao_account = res.kakao_account;
            const profile = kakao_account.profile_image;
            const nickname = kakao_account.profile_nickname;

            console.log('카카오 profile:', profile);
            console.log('카카오 nickname:', nickname);
            console.log('카카오 account:', kakao_account);

            kakaoLoggedIn.value = true;
            alert('카카오 로그인 성공!');
        },
        fail: (error) => {
            alert('카카오 로그인에 실패했습니다. 다시 시도해주세요.');
            console.log(error);
        }
    });
};

const kakaoLogout = () => {
    if (!window.Kakao.Auth.getAccessToken()) {
        console.log('카카오 로그인 상태가 아닙니다.');
        return;
    }

    window.Kakao.Auth.logout((response) => {
        console.log('카카오 access token:', window.Kakao.Auth.getAccessToken());
        console.log('카카오 log out:', response);
        kakaoLoggedIn.value = false;
        alert('카카오 로그아웃되었습니다.');
    });
};

// === 네이버 로그인 ===
const initializeNaver = async () => {
    await nextTick();

    try {
        const currentDomain = window.location.origin;
        // GitHub Pages를 위한 콜백 URL 설정
        const callbackUrl =
            process.env.NODE_ENV === 'production'
                ? `${currentDomain}/vuelogin/logintotal`
                : `${currentDomain}/logintotal`;

        naverLogin = new window.naver.LoginWithNaverId({
            clientId: naverClientId,
            callbackUrl: callbackUrl,
            isPopup: false,
            loginButton: { color: 'green', type: 3, height: 60, width: 250 }
        });

        naverLogin.init();
        console.log('✅ 네이버 로그인 초기화 완료');

        naverLogin.getLoginStatus((status) => {
            if (status) {
                checkNaverLoginStatus();
            } else {
                console.log('네이버 로그인되지 않은 상태입니다.');
            }
        });
    } catch (error) {
        console.error('네이버 로그인 초기화 중 오류 발생:', error);
    }
};

const checkNaverLoginStatus = () => {
    if (!naverLogin) {
        console.error('네이버 로그인 객체가 초기화되지 않았습니다.');
        return;
    }

    naverLogin.getLoginStatus((status) => {
        if (status) {
            const email = naverLogin.user.getEmail();
            if (!email) {
                alert('이메일은 필수정보입니다. 정보제공을 동의해주세요.');
                naverLogin.reprompt();
                return;
            }

            naverAccessToken = naverLogin.accessToken.accessToken;
            naverLoggedIn.value = true;
            console.log('네이버 로그인 성공!', email);
            alert('네이버 로그인 성공!');
        } else {
            console.log('네이버 callback 처리에 실패하였습니다.');
        }
    });
};

const naverLogout = () => {
    if (!naverAccessToken) {
        console.log('네이버 로그인 상태가 아닙니다.');
        return;
    }

    const logoutUrl = `https://nid.naver.com/oauth2.0/token?grant_type=delete&client_id=${naverClientId}&client_secret=FubU5cdFU2&access_token=${naverAccessToken}&service_provider=NAVER`;

    const iframe = document.createElement('iframe');
    iframe.style.display = 'none';
    iframe.src = logoutUrl;
    document.body.appendChild(iframe);

    naverAccessToken = null;
    naverLoggedIn.value = false;

    nextTick(() => {
        initializeNaver();
    });

    console.log('네이버 로그아웃 요청이 실행되었습니다.');
    alert('네이버 로그아웃되었습니다.');
};
</script>

<style scoped>
.login-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
}

.login-container h1 {
    color: #333;
    margin-bottom: 30px;
}

.login-section {
    margin-bottom: 40px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
}

.login-section h2 {
    color: #555;
    margin-bottom: 15px;
}

/* 구글 로그인 버튼 */
.google-button {
    background: none;
    border: none;
    cursor: pointer;
    padding: 0;
    margin-bottom: 10px;
    width: 250px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    overflow: hidden;
}

.google-button-img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    display: block;
}

.google-button:hover {
    opacity: 0.9;
}

/* 카카오 로그인 버튼 */
.kakao-button {
    cursor: pointer;
    display: inline-block;
    margin-bottom: 10px;
    width: 250px;
    height: 60px;
    border-radius: 8px;
    overflow: hidden;
}

.kakao-button img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.kakao-button:hover {
    opacity: 0.9;
}

/* 로그아웃 버튼들 */
.logout-button {
    padding: 10px 20px;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    cursor: pointer;
    margin-bottom: 10px;
}

.logout-button:hover {
    opacity: 0.8;
}

.google-logout {
    background-color: #4285f4;
}

.google-logout:hover {
    background-color: #357ae8;
}

.kakao-logout {
    background-color: #fee500;
    color: #191919;
}

.kakao-logout:hover {
    background-color: #f0d000;
}

.naver-logout {
    background-color: #03c75a;
}

.naver-logout:hover {
    background-color: #02b351;
}

/* 상태 표시 */
.status-section {
    margin-top: 30px;
    padding: 20px;
    background-color: #f0f0f0;
    border-radius: 8px;
}

.status-section h3 {
    color: #333;
    margin-bottom: 15px;
}

.status-section p {
    margin: 5px 0;
    color: #666;
    font-size: 14px;
}

/* 네이버 로그인 버튼 위치 조정 */
#naverIdLogin {
    display: inline-block;
}

/* 네이버 로그인 버튼 스타일 통일 */
#naverIdLogin table {
    width: 250px !important;
    height: 60px !important;
}

#naverIdLogin table td {
    width: 250px !important;
    height: 60px !important;
}

#naverIdLogin img {
    width: 100% !important;
    height: 100% !important;
    border-radius: 8px !important;
}
</style>
